.platform-win {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.justify-text {
  text-align: justify;
}

.fonts {
  font-size: 12px;
}

.bigger {
  width: 900px;
  padding: 32px 88px;
  margin: 32px;
}

.info-title {
  font-family: "Montserrat";
  font-size: 20px;
}

.info-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.info-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info-text {
  font-size: 14px;
  margin-top: 8px;
  text-align: justify;
  width: 200px;
}
